<script>
import gql from "graphql-tag";
import commonMixins from "@/mixins/common";

export default {
  name: "EntryMenus",
  mixins: [commonMixins],
  props: {
    classContent: {
      type: String,
      default: () => "",
    },
  },
  data() {
    return {
      dialogDelete: false,
      dialogReport: false,
      reason: "",
    };
  },
  methods: {
    handleShowModalDelete() {
      this.dialogDelete = true;
    },
    handleCloseModalDelete() {
      this.dialogDelete = false;
    },
    handleShowModalReport() {
      this.dialogReport = true;
    },
    handleCloseModalReport() {
      this.dialogReport = false;
    },
    onDelete() {
      this.$apollo
        .mutate({
          mutation: gql`
            mutation MyMutation($entryId: Int!, $userId: Int!) {
              deleteEntry(entry_id: $entryId, user_id: $userId) {
                result_code
              }
            }
          `,
          variables: {
            entryId: 1,
            userId: 1,
          },
          update: () => {},
        })
        .then(() => {
          this.dialogDelete = false;
          this.$router.push({ name: "placegallery-list" });
          setTimeout(() => {
            this.newToast({
              type: "success",
              message: "Delete entry success",
            });
          }, 0);
        })
        .catch((error) => {
          this.dialogDelete = false;
          if (error.graphQLErrors) {
            error.graphQLErrors.forEach(({ message }) => {
              this.newToast({
                type: "error",
                message: message,
              });
            });
          }
        });
    },
    onReport() {
      this.$apollo
        .mutate({
          mutation: gql`
            mutation MyMutation(
              $entryId: Int!
              $userId: Int!
              $reason: String!
            ) {
              createEntryReport(
                input: { entry_id: $entryId, user_id: $userId, reason: $reason }
              ) {
                result_code
              }
            }
          `,
          variables: {
            entryId: 1,
            userId: 1,
            reason: this.reason,
          },
          update: () => {},
        })
        .then(() => {
          this.dialogReport = false;
          setTimeout(() => {
            this.newToast({
              type: "success",
              message: "Report success",
            });
          }, 0);
        })
        .catch((error) => {
          this.dialogReport = false;
          if (error.graphQLErrors) {
            error.graphQLErrors.forEach(({ message }) => {
              this.newToast({
                type: "error",
                message: message,
              });
            });
          }
        });
    },
  },
};
</script>

