<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-toolbar-title>Post</v-toolbar-title>
        </v-toolbar>
        <div class="pm-pla-post">
          <div class="cs-pane">
            <div class="cs-pane__inner--a">
              <div class="pm-pla-form-container">
                <div class="pm-pla-form-container__list">
                  <div class="pm-pla-form-container__item">
                    <div class="pm-pla-form-container__title-group">
                      <div class="cs-title--common-m">/*</div>
                      <div class="cs-text-s">5</div>
                    </div>
                    <!-- /-->
                    <div id="media" class="pm-pla-post__media">
                      <div id="media-list" class="pm-pla-post__media-list mb-2">
                        <div v-for="(image, index) in images" :key="index">
                          <div
                            :ref="`image`"
                            class="pm-pla-post__media-upload"
                            :style="{
                              'background-image': 'url(' + image + ')',
                            }"
                          >
                            <span
                              class="cs-media-upload__button-delete"
                              @click="removeImage(index)"
                            ></span>
                          </div>
                        </div>
                      </div>
                      <div id="media-ui" class="pm-pla-post__media-ui">
                        <div
                          id="media-uploader"
                          class="pm-pla-post__media-uploader"
                        >
                          <div class="pm-pla-post__media-uploader-button-group">
                            <label>
                              <input
                                type="file"
                                name="file"
                                ref="file"
                                id="media-uploader-input"
                                multiple
                                accept="image/jpeg"
                                @change="addImage"
                              />
                              <div class="cs-media-upload__button-add"></div>
                            </label>
                          </div>
                        </div>
                        <div id="media-text" class="pm-pla-post__media-text">
                          
                        </div>
                      </div>
                      <!-- <div
                        id="media-error"
                        class="pm-pla-post__media-error-text"
                      >
                      error message
                      </div> -->
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <form action="" method="post">
            <div class="cs-pane">
              <div class="cs-pane__inner--a">
                <div class="pm-pla-form-container">
                  <div class="pm-pla-form-container__list">
                    <div class="pm-pla-form-container__item">
                      <div class="pm-pla-form-container__title-group">
                        <div class="cs-title--common-m">*</div>
                        <div class="pm-pla-post__limit-warning-text">
                          <span id="body-input-counter"></span>
                        </div>
                      </div>
                      <textarea
                        name="body"
                        data-csdraft="target"
                        data-csfield="h-auto"
                        data-pla-counter-limit="1000"
                        rows="5"
                        id="body-input"
                        v-model="form.description"
                        class="cs-field-a cs-field--w-100p"
                      ></textarea>
                      <div class="pm-pla-post__hashtags-title">
                        
                      </div>
                      <div class="pm-pla-post__hashtags-list">
                        <div id="tag-suggest" class="cs-tag-list">
                          <div class="cs-tag-list__item">
                            <span data-pla-tag-suggest class="cs-tag"
                              >#1</span
                            >
                          </div>
                          <div class="cs-tag-list__item">
                            <span data-pla-tag-suggest class="cs-tag"
                              >#2</span
                            >
                          </div>
                          <div class="cs-tag-list__item">
                            <span data-pla-tag-suggest class="cs-tag"
                              >#3</span
                            >
                          </div>
                          <div class="cs-tag-list__item">
                            <span data-pla-tag-suggest class="cs-tag"
                              >#4</span
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="cs-pane">
              <div class="cs-pane__inner--a">
                <div class="pm-pla-form-container">
                  <button
                    @click.prevent="handleSubmit"
                    type="submit"
                    id="submit-button"
                    class="cs-button-a--1-xl cs-button--w-100p"
                  >
                    
                  </button>
                </div>
                <div class="pm-pla-form-container">
                  <div
                    @click.prevent="handleClose"
                    data-csdraft="button"
                    class="cs-button-b--3-l cs-button--w-100p"
                  >
                    
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
import gql from "graphql-tag";
import commonMixins from "@/mixins/common";

export default {
  name: "PlacegalleryPost",
  mixins: [commonMixins],
  data() {
    return {
      dialog: true,
      form: {
        tags: [],
        items: [],
        cateId: 1,
        existImgs: [],
        description: "",
      },
      images: [],
      imageFiles: [],
      desLength: 0,
      isDisabled: false,
    };
  },
  watch: {
    imageFiles() {
      this.checkImageLimit(false);
    },
    form: {
      handler(val) {
        this.form = val;
      },
      deep: true,
    },
  },
  methods: {
    handleClose() {
      this.dialog = false;
      this.$emit("onClose");
    },
    async handleSubmit() {
      await this.$apollo
        .mutate({
          mutation: gql`
            mutation MyMutation(
              $caption: String!
              $moduleId: Int!
              $description: String!
              $images: String!
              $categoryId: Int!
              $userId: Int!
            ) {
              createEntry(
                input: {
                  caption: $caption
                  images: $images
                  module_id: $moduleId
                  description: $description
                  category_l_id: $categoryId
                  user_id: $userId
                }
              ) {
                result_code
                data {
                  caption
                  created
                  id
                  description
                }
              }
            }
          `,
          variables: {
            caption: this.form.description,
            moduleId: 1,
            description: this.form.description,
            images: this.images,
            categoryId: this.form.cateId,
            userId: 1,
          },
          context: {
            hasUpload: true,
          },
          update: () => {
            this.newToast({
              type: "success",
              message: "Create Post Success",
            });
          },
        })
        .then((data) => {
          this.$emit("onChangeData", data);
        })
        .catch((error) => {
          if (error.graphQLErrors) {
            error.graphQLErrors.forEach(({ message }) => {
              this.newToast({
                type: "error",
                message: message,
              });
            });
          }
        });

      await this.handleClose();
    },
    addImage(e) {
      const tmpFiles = e.target.files;
      if (tmpFiles.length === 0) {
        return false;
      }
      for (let i = 0; i < tmpFiles.length; i++) {
        if (this.checkImageFile(tmpFiles[i]) || this.checkImageLimit(true))
          return false;
        this.imageFiles.push(tmpFiles[i]);
        this.createImage(tmpFiles[i]);
      }
    },
    createImage(file) {
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => {
        vm.images.push(e.target.result);
      };
      reader.readAsDataURL(file);
      if (this.checkImageLimit(false)) {
        return false;
      }
    },
    removeImage(index) {
      if (this.checkImageLimit(false)) this.checkChooseFile(false);
      this.imageFiles.splice(index, 1);
      this.images.splice(index, 1);
    },
    checkImageLimit(isShowMessage = true) {
      if (this.imageFiles.length >= 5) {
        this.checkChooseFile();
        if (isShowMessage)
          this.setError("image-error", "");
        return true;
      }
      return false;
    },
    checkImageFile(file) {
      if (file.size > 1024 * 1024 * 10) {
        this.setError("image-error", "10MB");
        return true;
      }
      return false;
    },
    checkChooseFile(disabled = true) {
      this.$refs.file.disabled = disabled;
    },
    onUpdateImage(value) {
      console.log(value);
      this.images = value;
    },
  },
};
</script>

